import React, { useEffect, useState } from 'react';
import { useRecoilState } from 'recoil';
import { loginState } from '../../state/userNameState';

export default function LoginForm() {
  //   const setUserName = useSetRecoilState(userNameState);
  //useSetRecoilState :    setter .
  //   userName  

  const [loginInfo, setLoginInfo] = useRecoilState(loginState);
  const [input, setInput] = useState('');
  // const [loginState, setLoginState] = useState({
  //   isLogin: false,
  //   userName: '',
  // });
  const loginText = loginState.isLogin ? 'LOGOUT' : 'LOGIN';

  const onChangeInputHandler = (e) => {
    const text = e.target.value;
    setInput(text);
  };

  // const inputText = <input type="text" onChange={onChangeInputHandler} />;

  // const getUserName = window.localStorage.getItem('userName');

  //    data 
  // useEffect(() => {
  //   // const storedUserName = window.localStorage.getItem('userName');
  //   // if (storedUserName) {
  //   //   setLoginInfo({
  //   //     isLogin: true,
  //   //     userName: storedUserName,
  //   //   });
  //   // }
  //   // console.log()
  // }, [setLoginInfo]); //    [state.userName]   ?
  // console.log('loginstate >>', loginState);

  const onClickSubmitHandler = (e) => {
    e.preventDefault();
    if (!loginInfo.isLogin) {
      // false  
      setLoginInfo({
        isLogin: true,
        userName: input,
      });
      // setUserName(input);
    } else {
      setLoginInfo({
        isLogin: false,
        userName: '',
      });
    }

    setInput('');
  };

  return (
    <div>
      <span>
        {loginInfo.isLogin ? (
          <h2> {loginInfo.userName}</h2>
        ) : (
          <h2> X </h2>
        )}
      </span>
      <form>
        <input type="text" value={input} onChange={onChangeInputHandler} />
        <button type="button" onClick={onClickSubmitHandler}>
          {loginText}
        </button>
      </form>
    </div>
  );
}
